import React, { useState, useEffect } from 'react';
import { authenticate, logout } from '../authenticate';
import {useNavigate} from 'react-router-dom';
import {Link,Outlet} from 'react-router-dom';
import { Button } from 'react-bootstrap';
import './admin.css';





const Admin = () =>{

        var auth = authenticate();
        var nav = useNavigate();
        
        const mylogout = ()=>{
              var auth =  logout();
              if (auth === false){
                nav("/Login")
              }
        }

        useEffect(()=>{
            if (auth == false){
                nav("/Login")
            }
        })

    return(
        <div>
                <div className="container">
                        <div className='row'>
                                <div className='col-lg-12'>
                                        <ul className="myadmin-ul">
                                            <Link to="/admin/uploadImage"> Upload Image </Link>
                                            <Link to="/admin/message"> Broadcast Message </Link>
                                           
                                        </ul>
                                        <Button onClick={mylogout}>Logout</Button>
                                        
                                        

                                </div>
                        </div>
                </div>
                <div className="container mt-2">

                <Outlet></Outlet>
                </div>
               
        </div>)
} 

export default Admin;