import React, {useState} from 'react';
import {Form, Button,Alert} from 'react-bootstrap';
import axios from 'axios';





const Signup = ()=>{

     const [name, setName] = useState("");
     const [email, setEmail] = useState("");
     const [ph, setPh] = useState("");
     const [msg, setMsg] = useState("");

     const save = () =>{
        setMsg("");
        if (name == ""){
                setMsg("Please enter name")
        }else if ( email == ""){
            setMsg ( "Please enter email ID" )
        }else if ( ph.toString().length < 10){
              
             setMsg("Please enter 10 digit phone number")
           
        }else {
            var url = " http://localhost:1234/user";

            var list = {
                "name" :name,
                "email":email,
                "ph":ph,
                "id":ph
            }
            console.log(list)
                axios.post(url,list).then(response =>{
                    console.log(response.data)
                    setName("");
                    setEmail("");
                    setPh("");
                    setMsg(" Registered successfully");

                }).catch(err => {
                    
                    setMsg(" Phone number already exist");
                    
                });;
        }

     }


    return (<>

<div className="container">
                <div className='row'>
                        <div className='col-md-6 m-auto p-3'>
                                        <div>
                                          <Alert> {msg}</Alert> 
                                        </div>
                                <div className="login-box p-4 rounded">
                                        
                                        <div className='user-box'>
                                            <i className='fa fa-user-plus font-50x'></i>
                                        </div>    
                                        <span>  Registration form for live updates </span>

                                        <Form>
                                            <Form.Group className="mb-3">

                                                <Form.Label> Name </Form.Label>
                                                <Form.Control value={name} onChange={ e => { setName(e.target.value) }} type="text" placeholder="Enter name" />
                                            </Form.Group>
                                            <Form.Group className="mb-3">

                                                <Form.Label> Email Id </Form.Label>
                                                <Form.Control value={email} onChange={ e => { setEmail(e.target.value) }} type="email" placeholder="Enter email" />
                                            </Form.Group>

                                            <Form.Group className="mb-3" controlId="formBasicPassword">
                                                <Form.Label>Phone Number</Form.Label>
                                                <Form.Control value={ph} onChange={ e => { setPh(e.target.value) }} type="number" placeholder="Enter number" />
                                            </Form.Group>

                                            <Form.Group className="mb-3 text-center" controlId="formBasicCheckbox">
                                                <Button onClick={save} variant="light" type="submit">
                                                    Save
                                                </Button>
                                            </Form.Group>
                                            
                                        </Form>
                                </div>    
                        </div>
                </div>

            </div>
                

        </>)
}

export default Signup;