import React,{useState, useEffect} from 'react';
import {Link} from 'react-router-dom';
import { useLocation } from 'react-router-dom';
import { authenticate } from '../authenticate';
import './Navigator.css';



const Navigator = ()=>{
        const [active, setActive] = useState(['nav-active','','','','','']);

        var path = useLocation();
        var index = 0;


        var auth = authenticate();





        const HighlightLocation = (path) =>{ 
               
                var temp = active;
                switch(path){
                      case '/' : temp = ['nav-active','','','','','']; break;
                      case '/about' : temp = ['','nav-active','','','','']; break;
                      case '/contact' : temp = ['','','nav-active','','','']; break;
                      case '/gallery' : temp = ['','','','nav-active','','']; break;
                      case '/signup' : temp = ['','','','','nav-active','']; break;
                      case '/login' : temp = ['','','','','','nav-active'];     break   ;
                      case '/admin' : temp = ['','','','','','nav-active'];     break   ; 
                      case '/admin/message' : temp = ['','','','','','nav-active'];     break   ; 
                      case '/admin/uploadImage' : temp = ['','','','','','nav-active'];     break   ; 
                }
                setActive(temp);
               
        }
        useEffect( () =>{
                
                if (index == 0){

                        HighlightLocation(path.pathname);
                        index=1;
                }
                
               
        },[])
        return(
            <div className='container-fluid footer-bg  footer '>
            <div className='row'> 
                 <div className='col-lg-12 p-1 text-center'>
                         <li className={active[0]}> <Link onClick={() => HighlightLocation('/')}  id='a' to="/">Home </Link>   </li> 
                         <li className={active[1]}> <Link onClick={() => HighlightLocation('/about')} id='a' to="/about">About Us </Link> </li>
                         <li className={active[2]}> <Link onClick={() => HighlightLocation('/contact')} id='a' to="/contact">Contact Us </Link> </li>
                         <li className={active[3]}> <Link onClick={() => HighlightLocation('/gallery')} id='a' to="/gallery">Gallery </Link> </li>
                         <li className={active[4]}> <Link onClick={() => HighlightLocation('/signup')} id='a' to="/signup">Sign Up </Link> </li>
                        
                         <li className={active[5]}> <Link onClick={() => HighlightLocation('/login')} id='a' to= { auth ? "/admin/uploadImage" : "/Login"} > { auth ? "Admin" : "Login"} </Link> </li>
                         
                 </div>
                
             </div> 
     
     </div>)
            
         
}

export default Navigator;