import React,{useState} from 'react';
import {Link} from 'react-router-dom';
import { authenticate } from '../authenticate';
import { useLocation } from 'react-router-dom';


import "./Mobnavigator.css";



const Mobnavigator = () => {
    const [nav, updateNav] = useState(false);
     const update = ()=>{
        if (nav){
            updateNav(false);
        }else{
            updateNav(true);
        }
     }
     var auth = authenticate();

    return (
        <div className='container-fluid'>
             
            <div className='row'>
                <div className='col-lg-12 bg-warning'>
                        <div onClick={update} className="Mob-box ">
                             <i className="fa fa-bars my-nav-icon "> </i> 
                        </div>

                        <div className= { nav ? 'my-nav-popup-on' : 'my-nav-popup-off'  }>
                               <div onClick={update}> <i className='fa fa-close p-4 fa-2xl'></i> </div>
                               <div>
                                        <Link to='/'> <i className='fa fa-home p-3 fa-2xl'> Home</i> </Link>  
                               </div>
                               <div>
                                        <Link to='/about'>   <i className="fa fa-suitcase p-3 fa-2xl"> About Us</i></Link>  
                               </div>
                               <div>
                                        <Link to='/contact'>   <i className='fa fa-home p-3 fa-2xl'> Conact Us</i></Link>  
                               </div>
                               <div>
                                        <Link to='/gallery'>  <i className='fa fa-image p-3 fa-2xl'> Gallery</i></Link>  
                               </div>
                               <div>
                                         <Link  to="/signup"> <i className='fa fa-user-plus p-3 fa-2xl'> Sign Up</i> </Link> 
                               </div>
                               <div>
                                        <Link  to= { auth ? "/admin/uploadImage" : "/Login"} > <i className='fa fa-user p-3 fa-2xl'> { auth ? "Admin" : "Login"} </i>  </Link> 
                               </div>
                              
                            
                               
                        </div>
                            
                </div>
            </div>


        </div>)

}

export default Mobnavigator;