const authenticate = () => {

    var auth_token = localStorage.getItem("admin_account");
    //console.log(auth_token.email);
    var auth;

    if (auth_token ) {

        auth = true;
    } else {
        auth = false;
    }

    return auth;
}

const logout = () => {

    localStorage.removeItem("admin_account");

    var auth = false;

    return auth;

}

export { authenticate, logout };