import axios from 'axios';
import React,{useState, useEffect} from 'react';
import {Table, Form, Button,Alert} from 'react-bootstrap';


const Message = () =>{

        const [event, setEvent] = useState("");
        const [description, setDescription] = useState("");
        const [date, setDate] = useState("");
        const [msg, setMsg] = useState("");

        const[events, setEvents] = useState([]);

        
        
        const validate = ()=>{
            var s_url = "http://localhost:1234/events";

            var data = {
                "event" : event,
                "description" : description,
                "date" : date
            }

            setMsg("");

            
                if(event === ""){
                    setMsg("Please enter event")
                }else if(description === ""){
                    setMsg("Please enter description")
                }else if (date === ""){
                    setMsg("Please select date")
                }else {
                    axios.post(s_url, data).then(res=>{
                        setEvent("")
                        setDescription("");
                        setDate("");
                        setMsg("Event saved successfully")
                        getList();
                    })
                }
        }

        const getList = () =>{
            var myurl =  "http://localhost:1234/events";
                axios.get(myurl).then(res=>{
                    setEvents(res.data);
                })
        }

        const mydelete = (id) =>{
            var myurl = "http://localhost:1234/events/" + id;
            console.log(id)
            axios.delete(myurl).then(res=>{
                getList();
            }).catch(err =>{
                console.log(err);
            })
        }

        useEffect(() => {
            getList();
          }, [])

    return(
        <div>
                <div className="container">
                        <h3 className="m-2 text-center p-3 bg-info"> Broadcast Message </h3>
                        <div className='row'>
                            <div className='col-lg-4 m-auto'>
                            <Alert className="">{msg}</Alert>
                            <Form>
                                            <Form.Group className="mb-3">

                                                        <Form.Label> Event </Form.Label>
                                                         <Form.Control value={ event} onChange={e =>{setEvent(e.target.value)}} type="text" placeholder="" />
                                            </Form.Group>
                                            <Form.Group className="mb-3" controlId="">
                                                        <Form.Label>Description</Form.Label>
                                                        <Form.Control value={ description} onChange={e =>{setDescription(e.target.value)}} as="textarea" Style={{ height: '100px' }} placeholder="..." />
                                            </Form.Group>
                                            <Form.Group className="mb-3" controlId="">
                                                        <Form.Label>Date</Form.Label>
                                                        <Form.Control value={ date} onChange={e =>{setDate(e.target.value)}} type="date" placeholder="" />
                                            </Form.Group>


                                            <Form.Group className="mb-3 text-center" controlId="">
                                                        <Button onClick={validate} variant="primary" type="button">
                                                            Save
                                                        </Button>
                                            </Form.Group>
                                            
                            </Form>
                            </div>
                        </div>
                </div>
                <div className='container'>
                    <div className='row'>
                        <div className='col-lg-12'>
                                <Table>
                                        <thead>
                                            <tr>
                                                <th>Date</th>
                                                <th>Event </th>
                                                <th>Event Information </th>
                                                <th> Acton </th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                           {
                                                events.map((item,index)=>{
                                                    return(
                                                        <tr key={index}>
                                                            <td>{item.event}</td>
                                                            <td>{item.description}</td>
                                                            <td>{item.date}</td>
                                                            <td>    
                                                                <div className="f-size">
                                                                        <i onClick={ e =>{ mydelete(item.id)} } className='fa fa-trash'></i>   
                                                                </div>  
                                                            </td>
                                                        </tr>
                                                    )
                                                })

                                           } 
                                        </tbody>
                                </Table>
                        </div>
                    </div>
                </div>
                
        </div>)
}

export default Message;