import React from "react";




class Myimage extends React.Component {
        
    render(){
        return(
            <span>
           
          
                     <img src={this.props.src} alt={this.props.src} className="shadow m-2 p-2" width='200' height='200' />
           
    
    
            </span>

        )  

    }

}
    


export default Myimage;