import React, {useState, useEffect} from 'react';
import {Form, Button, Table, Alert} from 'react-bootstrap';
import axios from 'axios';
import './uploadImage.css';

const UploadImage = () =>{

    const [imageList, setList] = useState([]);
    const [name, setName]= useState("");
    const [url, setUrl] = useState("");
    const [msg, setMsg] = useState("");


    const getList = ()=>{
        var myurl =  "http://localhost:1234/images";
        axios.get(myurl).then(res=>{
            setList(res.data);
        })
    }

    const mydelete = (id) =>{
        var myurl = "http://localhost:1234/images/" + id;
        console.log(id)
        axios.delete(myurl).then(res=>{
            getList();
        }).catch(err =>{
            console.log(err);
        })

    }


    useEffect ( ()=>{
        getList();
    } , []) 

    const validate = () => {

        var s_url = "http://localhost:1234/images";

        var data = {
            "name" : name,
            "src" : url
        }

        if (name === ""){
                setMsg("Please enter the name")
        }else if ( url === ""){
            setMsg("Please enter the URL")
        }else {
            axios.post(s_url, data).then(res=>{
                setMsg("Image saved successfully")
                setName("");
                setUrl("");
                getList();
            })
            
        }


    }
    return(
        <div>
                <div className="container">
                <h3 className="m-2 text-center p-3 bg-info"> Upload Image </h3>
                    <div className="row">
                        <div className="col-lg-4 m-auto text-center">
                                <Alert className="">{msg}</Alert>
                                <Form>
                                    <Form.Control className="mb-2 p-2" value={name} onChange={e => {setName(e.target.value)}}  type="text" placeholder="Image Name">

                                    </Form.Control>
                                    <Form.Control className="mb-2 p-2" value={url} onChange={e => {setUrl(e.target.value)}} type="text" placeholder="Image URL">

                                    </Form.Control>

                                    <Form.Group className="mb-3 text-center" controlId="">
                                                        <Button onClick={validate} variant="primary" type="button">
                                                            Save
                                                        </Button>
                                    </Form.Group>

                                </Form>

                                <hr />
                        </div>
                    </div>
                </div>
                <div>
                    <div>
                        <div>
                            <Table>
                                    <thead>
                                        <tr> 
                                            <th>ID</th>
                                            <th>Name</th>
                                            <th>Image</th>
                                            <th>Action</th>
                                         </tr>
                                    </thead>
                                    <tbody>
                                        {
                                                imageList.map((item, index)=>{
                                                    return(
                                                        <tr key={index}>
                                                            <td> {item.id}</td>
                                                            <td> {item.name}</td>
                                                            <td> <img src={item.src} width="100" height="100" alt="" /></td>
                                                            <td>    
                                                                <div className="f-size">
                                                                        <i onClick={ e =>{ mydelete(item.id)} } className='fa fa-trash'></i>   
                                                                </div>  
                                                            </td>
                                                        </tr>
                                                    )
                                                })
                                        }
                                            
                                    </tbody>
                            </Table>
                        </div>
                    </div>
                </div>
        </div>)
}

export default UploadImage;