import React,{useState} from 'react';
import {useNavigate} from 'react-router-dom';
import {Form,Button, Alert} from 'react-bootstrap';
import axios from 'axios';
import './Login.css';




const Login = ()=>{

    const [email, setEmail] = useState("");
    const [password,setPassword] = useState("");
    const [msg, setMsg] = useState("");

    const nav = useNavigate();

    const validate = ( ) =>{
        setMsg("");
        if (email === "") {
            setMsg("Please enter the email Id")
        }else if( password === ""){
            setMsg("Please enter the password")
        }else {
            
            var url = " http://localhost:1234/account";

            axios.get(url).then( res => {
                    console.log(res.data)
                    if ( email === res.data.email  && password === res.data.password ) {
                            setMsg("Logged in succssfully");
                            localStorage.setItem("admin_account", res.data);
                            nav("/admin/uploadImage");
                    }else{
                        setMsg("Wrong email or password");
                    }

            })
        }

    }

    return (<>
           
            <div className="container">
                <div className='row'>
                        <div className='col-md-6 m-auto p-3'>
                                        <div>
                                            <Alert> {msg}</Alert> 
                                        </div>
                                <div className="login-box p-4 rounded">
                                        <div className='user-box'>
                                            <i className='fa fa-user font-50x'></i>
                                        </div>    
                                        <span>  Admin Login</span>

                                        <Form>
                                            <Form.Group>

                                                        <Form.Label> Email Address </Form.Label>
                                                         <Form.Control value={ email} onChange={e =>{setEmail(e.target.value)}} type="email" placeholder="Enter email" />
                                            </Form.Group>
                                            <Form.Group className="mb-3" controlId="formBasicPassword">
                                                        <Form.Label>Password</Form.Label>
                                                        <Form.Control value={ password} onChange={e =>{setPassword(e.target.value)}} type="password" placeholder="Password" />
                                            </Form.Group>
                                            <Form.Group className="mb-3 text-center" controlId="formBasicCheckbox">
                                                        <Button onClick={validate} variant="light" type="button">
                                                            Login
                                                        </Button>
                                            </Form.Group>
                                            
                                        </Form>
                                </div>    
                        </div>
                </div>

            </div>


                
        </>)
}

export default Login;